.word-list-9yf {
  & > .word {

    font-weight: bold;
    color: #9b5e27;

    display: inline-block;
    padding: 3px 5px;

    @media screen and (min-width: 768px) {
      display: block;
      padding: 5px 15px;
    }

    &.selected {
      //text-decoration: line-through;
      background: #d8fad8;
      color: #037203;
    }

    &.disabled {
      color: #d4d4d4;
    }
  }
}