.game-1i1 {
  padding-top: 40px;
  text-align: center;
  background: #d6d6d6;
  min-height: 100vh;

  & > .box {
    display: inline-block;
    background: white;
    padding: 15px;
    border-radius: 5px;
    border: 1px solid #d6d6d6;
    box-shadow: 2px 2px 4px rgba(black, .1);

    & > .line1 {
      display: flex;
      flex-direction: column;
      align-items: center;

      @media screen and (min-width: 768px) {
        white-space: nowrap;
      }

      & > .word-list {
        display: inline-block;
        vertical-align: top;
        margin-top: 10px;

        order: 1;

        @media screen and (min-width: 768px) {
          order: 0;
          margin-top: 0;
          margin-right: 40px;
        }
      }

      & > .grid {
        display: inline-block;
      }
    }

    & > .status {
      & > .congratulations {
        font-size: 30px;
        font-weight: 600;
        padding: 30px 0;
        color: #037203;
      }
    }

    .controls {
      margin-top: 40px;
      text-align: right;
    }
  }
}